<template>
  <div>
    <div class="center">
      <div class="left">
        <div class="head">
          <img :src="UserInfo.avatar" alt v-if="UserInfo.avatar !=null">
          <img src="../../../assets/img/head.png" alt v-else>
        </div>
      </div>
      <div class="right">
        <p class="name">{{UserInfo.nickname}}</p>
        <p class="signatures">{{UserInfo.sign}}</p>
        <div class="tab">
          <el-tag size="small" v-if="UserInfo.gender==''? false:true">{{UserInfo.gender}}</el-tag>
          <el-tag
            size="small"
            v-if="UserInfo.school.school==''? false:true"
          >{{UserInfo.school.school}}</el-tag>
          <el-tag size="small" v-if="UserInfo.industry==''? false:true">{{UserInfo.industry}}</el-tag>
        </div>
      </div>
    </div>
    <div class="link_header lightest-bg">消息中心</div>
    <div class="datasete">
      <div class="editor assistant-text">编辑个人资料</div>
      <div class="editorHeader">
        <span>头像编辑</span>
        <div class="headImgback"></div>
        <div class="pic-wrapper">
          <img :src="pic">
        </div>
        <a href="javascript:void(0)" class="file">
          选择文件
          <input
            type="file"
            name="select-pic"
            id="select-pic"
            accept="image/*"
            @change="previewPic"
          >
        </a>
      </div>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
        :label-position="labelPosition"
      >
        <el-form-item label="昵称" prop="name" class="name">
          <el-col :span="15">
            <el-input v-model="ruleForm.nickname" placeholder="请输入昵称" :disabled="nameShow"></el-input>
            <el-button
              type="text"
              style="margin-left:20px"
              @click="nameClick"
            >{{nameShow?'编辑':'保存'}}</el-button>
          </el-col>
        </el-form-item>
        <el-form-item label="个性签名" prop="sign " class="sign">
          <el-col :span="15">
            <el-input
              type="textarea"
              v-model="ruleForm.sign "
              :autosize="{ minRows: 2, maxRows: 4}"
              :disabled="signcShow"
            ></el-input>
            <el-button
              type="text"
              style="margin-left:20px"
              @click="signcClick"
            >{{signcShow?'编辑':'保存'}}</el-button>
          </el-col>
        </el-form-item>
        <el-form-item label="手机号">
          <el-col :span="3">
            <el-form-item prop>
              <el-select v-model="ruleForm.place" :disabled="phoneShow">
                <el-option label="中国大陆" value="86"></el-option>
                <el-option label="中国香港" value="852"></el-option>
                <el-option label="中国澳门" value="853"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item prop="phone">
              <el-input v-model="ruleForm.phone" autocomplete="off" :disabled="phoneShow"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="3">
            <el-button
              type="text"
              style="margin-left:20px"
              @click="phoneClick"
            >{{phoneShow?'编辑':'保存'}}</el-button>
          </el-col>
        </el-form-item>

        <el-form-item label="性别">
          <el-col :span="8">
            <el-select v-model="ruleForm.gender" class="gender" :disabled="genderShow">
              <el-option label="女" value="女"></el-option>
              <el-option label="男" value="男"></el-option>
            </el-select>
            <el-button
              type="text"
              style="margin-left:20px"
              @click="genderClick"
            >{{genderShow?'编辑':'保存'}}</el-button>
          </el-col>
        </el-form-item>
        <el-form-item label="生日">
          <el-col :span="8">
            <el-date-picker v-model="ruleForm.birthday" type="date" :disabled="birthdayShow"></el-date-picker>
            <el-button
              type="text"
              style="margin-left:20px"
              @click="birthdayClick"
            >{{birthdayShow?'编辑':'保存'}}</el-button>
          </el-col>
        </el-form-item>

        <el-form-item label="行业">
          <el-col :span="15">
            <el-input v-model="ruleForm.industry" class="industry" :disabled="industryShow"></el-input>
            <el-button
              type="text"
              style="margin-left:20px"
              @click="industryClick"
            >{{industryShow?'编辑':'保存'}}</el-button>
          </el-col>
        </el-form-item>

        <el-row>
          <el-col :span="8">
            <el-form-item label="学校">
              <el-input v-model="ruleForm.school" :disabled="schoolShow"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="入学时间">
              <el-input v-model="ruleForm.year" :disabled="schoolShow"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <el-form-item label="专业">
              <el-input v-model="ruleForm.specialty" :disabled="schoolShow"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item>
              <el-checkbox v-model="checked" :disabled="schoolShow">公开(对所有人可见)</el-checkbox>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item>
              <el-button
                type="text"
                style="margin-left:20px"
                @click="schoolClick"
              >{{schoolShow?'编辑':'保存'}}</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    var checkphone = (rule, value, callback) => {
      // let phoneReg = /(^1[3|4|5|6|7|8|9]\d{9}$)|(^09\d{8}$)/;
      if (value == "") {
        callback(new Error("请输入手机号"));
      } else if (!this.isCellPhone(value)) {
        //引入methods中封装的检查手机格式的方法
        callback(new Error("请输入正确的手机号!"));
      } else {
        callback();
      }
    };
    return {
      pic: "",
      labelPosition: "right",
      nameShow: true,
      signcShow: true,
      phoneShow: true,
      genderShow: true,
      birthdayShow: true,
      industryShow: true,
      schoolShow: true,
      checked: false,
      checked1: "",
      UserInfoList: "",
      Useravatar: "",
      ruleForm: {
        nickname: "",
        sign: "",
        place: "",
        phone: "",
        gender: "",
        birthday: "",
        industry: "",
        school: "",
        specialty: "",
        year: "",
        file: ""
      },
      rules: {
        phone: [
          {
            required: true,
            validator: checkphone,
            trigger: "blur"
          } //validator: checkphone是表单验证
          // { type: "number", message: "手机必须为数字值" }
        ]
      }
    };
  },
  mounted() {
    this.getUserInfo();
  },
  methods: {
    // 手机号验证正则表达试
    isCellPhone(val) {
      if (!/^1(3|4|5|6|7|8)\d{9}$/.test(val)) {
        return false;
      } else {
        return true;
      }
    },
    // 头像编辑
    previewPic: function(e) {
      var that = this;
      this.file = e.target.files[0];
      this.avatarEdit();
    },
    getUserInfo() {
      var url = this.APIURL + "/classic/usr/user/info";
      this.$axios
        .post(url, "", { headers: { SessionKey: this.SessionKey,key:this.key } })
        .then(({ data }) => {
          // console.log(data);
          if (data.result == "suc") {
            localStorage.setItem("userInfo", JSON.stringify(data.data));
            this.$store.commit("setUserInfo", data.data);
            this.ruleForm.nickname = data.data.nickname;
            this.ruleForm.sign = data.data.sign;
            this.ruleForm.phone = data.data.phone;
            this.ruleForm.gender = data.data.gender;
            this.ruleForm.birthday = data.data.birthday;
            this.ruleForm.industry = data.data.industry;
            this.ruleForm.school = data.data.school.school;
            this.ruleForm.year = data.data.school.year;
            this.ruleForm.specialty = data.data.school.specialty;
            this.pic = data.data.avatar;
            this.Useravatar = data.data.avatar;
          }
        });
    },
    // 昵称编辑按钮
    nameClick() {
      this.nameShow = !this.nameShow;
      if (this.nameShow == true) {
        this.userEdit("nickname", this.ruleForm.nickname);
      }
    },
    // 个性签名
    signcClick() {
      this.signcShow = !this.signcShow;
      if (this.signcShow == true) {
        this.userEdit("sign", this.ruleForm.sign);
      }
    },
    // 手机修改
    phoneClick() {
      this.phoneShow = !this.phoneShow;
      if (this.phoneShow == true) {
        this.userEdit("phone", this.ruleForm.phone);
      }
    },
    // 性别修改
    genderClick() {
      this.genderShow = !this.genderShow;
      if (this.genderShow == true) {
        this.userEdit("gender", this.ruleForm.gender);
      }
    },
    // 生日修改
    birthdayClick() {
      this.birthdayShow = !this.birthdayShow;
      if (this.birthdayShow == true) {
        this.userEdit("birthday", this.ruleForm.birthday);
      }
    },
    // 行业修改
    industryClick() {
      this.industryShow = !this.industryShow;
      if (this.industryShow == true) {
        this.userEdit("industry", this.ruleForm.industry);
      }
    },
    //学校修改
    schoolClick() {
      this.schoolShow = !this.schoolShow;
      if (this.schoolShow == true) {
        var url = this.APIURL + "/classic/usr/user/edit";
        if (this.checked == false) {
          this.checked1 = 0;
        } else {
          this.checked1 = 1;
        }
        var formdata = new FormData();
        formdata.append("type", "school");
        formdata.append("value[school]", this.ruleForm.school);
        formdata.append("value[specialty]", this.ruleForm.specialty);
        formdata.append("value[year]", this.ruleForm.year);
        formdata.append("value[visible]", this.checked1);
        this.$axios
          .post(url, formdata, {
            headers: { SessionKey: this.SessionKey,key:this.key }
          })
          .then(res => {
            console.log(res.data);
            if (res.data.errorCode == 0) {
              // this.getUserInfo();
              this.$message({
                type: "success",
                message: "修改成功!"
              });
            } else {
              alert(res.data.msg);
            }
          })
          .catch(err => {});
      }
    },
    // 头像上传方法
    avatarEdit() {
      var _this = this;
      var url = this.APIURL + "/classic/usr/user/avatar";
      var formdata = new FormData();
      formdata.append("file", this.file);
      this.$axios
        .post(url, formdata, {
          headers: {
            SessionKey: this.SessionKey,key:this.key
          }
        })
        .then(res => {
          console.log(res.data);
          // console.log(res.data);
          if (res.data.errorCode == 0) {
            this.getUserInfo();
            this.$message({
              type: "success",
              message: "修改成功!"
            });
          } else {
            alert(res.data.msg);
          }
        })
        .catch(err => {});
    },
    // 修改公共的方法
    userEdit(type, value) {
      var _this = this;
      var url = this.APIURL + "/classic/usr/user/edit";
      var data = {
        type: type,
        value: value
      };
      this.$axios
        .post(url, data, {
          headers: {
            SessionKey: this.SessionKey,key:this.key
          }
        })
        .then(res => {
          console.log(res.data);
          if (res.data.errorCode == 0) {
            this.getUserInfo();
            this.$message({
              type: "success",
              message: "修改成功!"
            });
          } else {
            alert(res.data.msg);
          }
        })
        .catch(err => {});
    }
  },
  
  computed: {
    UserInfo(val) {
      return this.$store.state.UserInfo;
    },
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    }
  }
};
</script>
<style lang="scss">
.center {
  width: 100%;
  height: 211px;
  background: rgba(255, 255, 255, 0.6);
  margin-bottom: 8px;
  .left {
    width: 140px;
    height: 211px;
    float: left;
    .head {
      width: 118px;
      height: 118px;
      margin: 18px 0 0 16px;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .right {
    float: left;
    width: 840px;
    height: 211px;
    // background: blue;
    .name {
      opacity: 1;
      font-size: 24px;
      font-family: PingFang SC Medium;
      color: rgba(51, 51, 51, 1);
      line-height: 48px;
      margin: 26px 0 0 10px;
    }
    .signatures {
      opacity: 1;
      font-size: 16px;
      font-family: PingFang SC Regular;
      color: rgba(51, 51, 51, 1);
      line-height: 24px;
      margin-left: 10px;
    }
    .tab {
      margin: 15px 0 0 10px;
      .el-tag {
        margin-right: 12px;
      }
    }
  }
}
.datasete {
  width: 100%;
  height: 860px;
  background: rgba(255, 255, 255, 0.6);
  .editor {
    opacity: 1;
    font-size: 14px;
    font-family: PingFang SC Regular;
    margin: 10px 0 0 18px;
  }
  .editorHeader {
    position: relative;
    width: 100%;
    height: 120px;
    margin: 28px 0 18px 0;
    padding-left: 30px;
    span {
      display: inline-block;
      font-size: 14px;
      color: #606266;
      line-height: 118px;
      overflow: hidden;
    }
    .headImgback {
      position: absolute;
      display: inline-block;
      width: 118px;
      height: 118px;
      border-radius: 50%;
      background: #000;
      opacity: 0.5;
      margin-left: 15px;
    }
    .pic-wrapper {
      display: inline-block;
      width: 118px;
      height: 118px;
      border: 1px dashed #000;
      margin-left: 15px;
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
      a {
        position: absolute;
        text-decoration: none;
        top: 70px;
        left: 133px;
        font-family: PingFang SC Regular;
        font-size: 14px;
      }
    }
    .file {
      position: absolute;
      display: inline-block;
      // background: #d0eeff;
      // border: 1px solid #99d3f5;
      border-radius: 4px;
      padding: 4px 12px;
      overflow: hidden;
      color: #fff;
      text-decoration: none;
      text-indent: 0;
      line-height: 20px;
      left: 113px;
      top: 81px;
    }
    .file input {
      position: absolute;
      font-size: 100px;
      right: 0;
      top: 0;
      opacity: 0;
    }
  }
}
.datasete .name .el-input {
  width: 73%;
}
.datasete .sign .el-textarea {
  width: 73%;
}
.datasete .v-phone {
  width: 70%;
}
.datasete .industry {
  width: 73%;
}
</style>
